},[_vm._v("Frente a las técnicas de análisis del flujo de los procesos existen herramientas que se usan para comprender el bienestar de las operaciones de la empresa en donde se mejore la eficiencia de los procesos dentro del contexto comercial. La gestión de los procesos se analiza identificando los elementos que son perjudiciales e interpretando cómo se van a superar los obstáculos, pues no por presionar más hay mejores resultados. A continuación algunas de estas técnicas que permiten analizar los procesos:")]),_c('TabsA',{staticClass:"color-acento-contenido mt-4 cont_tarjeta_custom",attrs:{"data-aos":"zoom-in"}},[_c('div',{staticClass:"tarjeta color-acento-contenido p-4",staticStyle:{"background-color":"#FBFAFF"},attrs:{"titulo":"Sistema <em>flow shop</em>"}},[_c('h5',[_vm._v("Sistema "),_c('em',[_vm._v("flow shop")])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-lg-7 mb-4 mb-lg-0"},[_c('p',[_vm._v("El sistema flow shop es denominado taller de flujo, útil en los flujos de trabajos fijos y con cantidades de producción ampliadas. En un taller de flujo el proceso de fabricación sigue una estructura lineal establecida. Eso significa que todos los pedidos deben fabricarse de la misma manera en las mismas máquinas. ")]),_c('p',{staticClass:"mt-3"},[_vm._v("La demanda en los talleres de flujo es constante y predecible, esto significa que la variabilidad de los datos es baja. La variedad del producto suele ser baja, ya que hay pocos productos y muy estandarizados. No se lleva con frecuencia la configuración de las máquinas de producción, puesto que no hay variedad de productos, por tanto, existe la posibilidad de reunir operaciones similares en un lote para impulsar la capacidad de producción. ")])]),_c('div',{staticClass:"col-lg-5"},[_c('figure',{staticClass:"m-auto col-6 col-md-7 col-lg-12"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/25.png")}})])])])]),_c('div',{staticClass:"tarjeta color-acento-contenido p-4",staticStyle:{"background-color":"#