 sistema calcula las cantidades planeadas, las duraciones y las fechas. Si la ejecución de la producción produce menos o más de lo que se planeó originalmente, esto debe informarse al área de planeación. La planeación debe, entonces, considerar cómo lidiar con la demanda insatisfecha o satisfecha.")]),_c('p',{staticClass:"mt-4",attrs:{"data-aos":"fade-left"}},[_vm._v("Los datos más representativos que se calculan a partir de las tareas de producción se establecen en la orden, la cual muestra lo que se planeó inicialmente y cuál ha sido el desempeño y cumplimiento del plan. De ahí la importancia de conocer inicialmente cada uno de sus estados:")]),_vm._m(28),_c('p',{staticClass:"mt-5",attrs:{"data-aos":"fade-right"}},[_vm._v("A continuación, se describe los principales datos que se verifican para saber hasta qué punto se ha desarrollado la planeación de la producción inicialmente con la orden de fabricación:")]),_c('div',{staticClass:"row mt-5"},[_c('div',{staticClass:"col-lg-5 mb-4 mb-lg-0",attrs:{"data-aos":"fade-right"}},[_c('LineaTiempoD',{staticClass:"color-acento-contenido"},[_c('p',{attrs:{"numero":"1","titulo":"Cantidad planeada de producción"}},[_vm._v("Es la cantidad de salida para la que se creó esta orden de producción. El sistema propone esta cantidad, pero se puede cambiar manualmente siempre que la orden de producción no esté en el estado cancelado, cerrado o finalizado.")]),_c('p',{attrs:{"numero":"2","titulo":"Cantidad  cumplida de producción"}},[_vm._v("Es la cantidad total del producto de salida que ha sido cumplida por la ejecución de la producción y notificada en lotes de fabricación.")]),_c('p',{attrs:{"numero":"3","titulo":"Cantidad  abierta"}},[_vm._v("Es la cantidad restante, la diferencia de la cantidad cumplida menos la cantidad planeada.")])])],1),_vm._m(29)]),_c('div',{staticClass:"row mt-4"},[_vm._m(30),_c('div',{staticClass:"col-lg-5",attrs:{"data-aos":"fade-left"}},[_c('LineaTiempoD',{staticClass:"color-acento-contenido"},[_c('p',{attr